'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { useRemoteControl, useTelemetry } from '@/hooks'
import { 
  Power, 
  ChevronUp, 
  ChevronDown,
  Wind,
  Snowflake,
  Sun,
  Droplets,
  Fan,
  Gauge
} from 'lucide-react'
import { cn } from '@/lib/utils'
import type { ACMode, FanSpeed } from '@/lib/types'

interface ACRemoteProps {
  deviceId: string
  deviceName: string
}

export function ACRemote({ deviceId, deviceName }: ACRemoteProps) {
  const { sendCommand, isLoading, ackStatus } = useRemoteControl(deviceId)
  const { latest } = useTelemetry(deviceId, 1)
  
  // Local state untuk UI (sinkron dengan data terbaru dari MQTT)
  const [power, setPower] = useState(false)
  const [temperature, setTemperature] = useState(24)
  const [mode, setMode] = useState<ACMode>('COOL')
  const [fanSpeed, setFanSpeed] = useState<FanSpeed>('AUTO')

  // Sync dengan data terbaru dari ESP32
  useEffect(() => {
    if (latest) {
      setPower(latest.power)
      setTemperature(latest.current_temp)
      setMode(latest.mode as ACMode)
      setFanSpeed(latest.fan_speed as FanSpeed)
    }
  }, [latest])

  // Mode options dengan icon
  const modeOptions: { mode: ACMode; icon: any; label: string; color: string }[] = [
    { mode: 'COOL', icon: Snowflake, label: 'Cool', color: 'text-blue-500 bg-blue-50 hover:bg-blue-100' },
    { mode: 'DRY', icon: Droplets, label: 'Dry', color: 'text-cyan-500 bg-cyan-50 hover:bg-cyan-100' },
    { mode: 'FAN', icon: Fan, label: 'Fan', color: 'text-gray-500 bg-gray-50 hover:bg-gray-100' },
    { mode: 'HEAT', icon: Sun, label: 'Heat', color: 'text-orange-500 bg-orange-50 hover:bg-orange-100' },
    { mode: 'AUTO', icon: Gauge, label: 'Auto', color: 'text-purple-500 bg-purple-50 hover:bg-purple-100' },
  ]

  const fanOptions: FanSpeed[] = ['AUTO', 'LOW', 'MID', 'HIGH']

  // Handler functions
  const handlePowerToggle = () => {
    const newPower = !power
    setPower(newPower)
    sendCommand({ power: newPower })
  }

  const handleTempIncrease = () => {
    if (temperature < 30) {
      const newTemp = temperature + 1
      setTemperature(newTemp)
      sendCommand({ temp: newTemp })
    }
  }

  const handleTempDecrease = () => {
    if (temperature > 16) {
      const newTemp = temperature - 1
      setTemperature(newTemp)
      sendCommand({ temp: newTemp })
    }
  }

  const handleModeChange = (newMode: ACMode) => {
    setMode(newMode)
    sendCommand({ mode: newMode })
  }

  const handleFanChange = (newFan: FanSpeed) => {
    setFanSpeed(newFan)
    sendCommand({ fan: newFan })
  }

  return (
    <Card className="max-w-md mx-auto shadow-2xl">
      <CardHeader className="bg-gradient-to-r from-blue-500 to-purple-600 text-white rounded-t-xl">
        <CardTitle className="flex items-center justify-between">
          <span>{deviceName}</span>
          {ackStatus && (
            <span className={cn(
              "text-xs px-2 py-1 rounded-full",
              ackStatus === 'success' 
                ? "bg-green-500/20 text-green-100" 
                : "bg-red-500/20 text-red-100"
            )}>
              {ackStatus === 'success' ? '✓ Sent' : '✗ Failed'}
            </span>
          )}
        </CardTitle>
      </CardHeader>

      <CardContent className="p-6 space-y-6">
        {/* Power Button */}
        <div className="flex justify-center">
          <Button
            size="xl"
            onClick={handlePowerToggle}
            disabled={isLoading}
            className={cn(
              "rounded-full w-24 h-24 shadow-lg transition-all",
              power
                ? "bg-gradient-to-br from-green-400 to-green-600 hover:from-green-500 hover:to-green-700"
                : "bg-gradient-to-br from-gray-300 to-gray-400 hover:from-gray-400 hover:to-gray-500"
            )}
          >
            <Power className={cn(
              "h-10 w-10",
              power ? "text-white" : "text-gray-600"
            )} />
          </Button>
        </div>

        {/* Temperature Control */}
        <div className="bg-gradient-to-br from-blue-50 to-purple-50 rounded-2xl p-6">
          <div className="text-center mb-4">
            <div className="text-sm text-gray-600 mb-1">Temperature</div>
            <div className="text-6xl font-bold text-blue-600">
              {temperature}°
            </div>
          </div>
          
          <div className="flex items-center justify-center gap-4">
            <Button
              size="icon"
              variant="outline"
              onClick={handleTempDecrease}
              disabled={isLoading || temperature <= 16}
              className="h-14 w-14 rounded-full border-2 border-blue-500 hover:bg-blue-50"
            >
              <ChevronDown className="h-6 w-6 text-blue-500" />
            </Button>
            
            <div className="w-32 h-2 bg-gradient-to-r from-blue-300 via-green-300 to-red-300 rounded-full" />
            
            <Button
              size="icon"
              variant="outline"
              onClick={handleTempIncrease}
              disabled={isLoading || temperature >= 30}
              className="h-14 w-14 rounded-full border-2 border-blue-500 hover:bg-blue-50"
            >
              <ChevronUp className="h-6 w-6 text-blue-500" />
            </Button>
          </div>
          
          <div className="flex justify-between text-xs text-gray-500 mt-2 px-2">
            <span>16°C</span>
            <span>30°C</span>
          </div>
        </div>

        {/* Mode Selection */}
        <div>
          <div className="text-sm font-medium text-gray-700 mb-3 text-center">
            Mode
          </div>
          <div className="grid grid-cols-5 gap-2">
            {modeOptions.map(({ mode: modeOption, icon: Icon, label, color }) => (
              <button
                key={modeOption}
                onClick={() => handleModeChange(modeOption)}
                disabled={isLoading}
                className={cn(
                  "flex flex-col items-center gap-1 p-3 rounded-lg transition-all",
                  mode === modeOption
                    ? color + " ring-2 ring-offset-2 ring-current"
                    : "bg-gray-50 text-gray-400 hover:bg-gray-100"
                )}
              >
                <Icon className="h-6 w-6" />
                <span className="text-xs font-medium">{label}</span>
              </button>
            ))}
          </div>
        </div>

        {/* Fan Speed */}
        <div>
          <div className="text-sm font-medium text-gray-700 mb-3 text-center">
            Fan Speed
          </div>
          <div className="grid grid-cols-4 gap-2">
            {fanOptions.map((fan) => (
              <Button
                key={fan}
                onClick={() => handleFanChange(fan)}
                disabled={isLoading}
                variant={fanSpeed === fan ? "default" : "outline"}
                className={cn(
                  "flex flex-col items-center gap-1 h-auto py-3",
                  fanSpeed === fan && "bg-gradient-to-br from-blue-500 to-purple-600"
                )}
              >
                <Wind className="h-5 w-5" />
                <span className="text-xs">{fan}</span>
              </Button>
            ))}
          </div>
        </div>

        {/* Current Status Display */}
        <div className="bg-gray-50 rounded-lg p-4 text-center">
          <div className="text-xs text-gray-600 mb-1">Current Status</div>
          <div className="text-sm font-medium">
            {power ? (
              <span className="text-green-600">
                Power ON • {mode} Mode • {fanSpeed} Fan • {temperature}°C
              </span>
            ) : (
              <span className="text-gray-600">Power OFF</span>
            )}
          </div>
        </div>
      </CardContent>
    </Card>
  )
}